
import { defineComponent, getCurrentInstance, toRefs, ref } from "vue";
import BaoyanIcon from "@/components/layouts/BaoyanIcon.vue";
import mixin from "@/mixins/mixin";
import { Icon } from "@/enums";
import { HttpManager } from "@/api";

export default defineComponent({
  components: {
    BaoyanIcon,
  },
  props: {
    title: String,
    playList: Array,
    path: String,
  },
  setup(props) {
    const { proxy } = getCurrentInstance();
    const { routerManager, getImageUrl } = mixin();
    const { path } = toRefs(props);

    const videoPath = ref("video")

    function goAblum(item) {
      routerManager(videoPath.value, { path: `/${videoPath.value}/${item.id}` });
    }

    return {
      BOFANG: Icon.BOFANG,
      goAblum,
      attachImageUrl: HttpManager.attachImageUrl,
      getImageUrl,
    };
  },
});
